import interfaces from '../../utils/interfaces.js';
export default {
	data() {
		return {
			goodsList: [],
			page: 1,
			size: 6,
			filterby: 'all',
			filterByList: [{
					title: "全部",
					selected: true,
					filterby: 'all'
				},
				{
					title: "口碑",
					selected: false,
					filterby: 'public'
				},
				{
					title: "热门",
					selected: false,
					filterby: 'hot'
				}
			],
			loadingText: "正在加载....",
			hasMore: true
		}
	},
	onLoad(option) {
		uni.setNavigationBarTitle({
			title: option.name
		})

		this.initData()
	},
	onPullDownRefresh() {
		setTimeout(() => {
			this.hasMore = true;
			this.goodsList = [];
			this.page = 1;
			this.loadingText = "加载中...";
			this.initData();
			uni.stopPullDownRefresh()
		}, 1000)
	},
	onReachBottom() {
		if (this.hasMore) {
			this.page++;
			this.initData();
		}
	},
	methods: {
		handleGoods(goods) {
			uni.navigateTo({
				url: './goods?goodsInfo=' + JSON.stringify(goods)
			})
		},
		handleSelect(index) {
			this.filterByList[index].selected = true;
			this.filterByList.forEach((item, i) => {
				if (i != index) {
					this.filterByList[i].selected = false
				}
			})
			this.hasMore = true;
			this.filterby = this.filterByList[index].filterby;
			this.page = 1;
			this.loadingText = "加载中...";
			this.goodsList = [];
			this.initData();
		},
		initData() {
			this.request({
				url: `${interfaces.getGoodsList}/${this.filterby}/${this.page}/${this.size}`,
				success: (res) => {
					if (res.data.length > 0) {
						res.data.forEach(item => {
							this.goodsList.push(item)
						})
					} else {
						this.loadingText = "到底了";
						this.hasMore = false
					}
				}
			})
		}
	}
}
